import { Component, OnDestroy, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { SearchGridComponent } from './search-grid.component';
import { FilteringActionType, AlertService, Status, gettext, Alert } from '@c8y/ngx-components';

@Component({
  selector: 'c8y-search-results',
  templateUrl: './search-results.component.html'
})
export class SearchResultsComponent implements OnDestroy {
  filter = '';
  searchText = '';
  @ViewChild(SearchGridComponent, { static: true })
  searchGrid: SearchGridComponent;
  filteringName: string;

  private readonly WARNING_TIMEOUT_TIME = 3000;
  private unsubscribe$ = new Subject<any>();

  constructor(private route: ActivatedRoute, private alert: AlertService) {}

  ngOnInit() {
    this.route.queryParams.pipe(takeUntil(this.unsubscribe$)).subscribe(params => {
      if (params.filter) {
        this.filteringName = params.filter;
      }
    });
  }

  ngAfterViewInit(): void {
    this.route.queryParams
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(({ filter, search }) => this.onQueryParamsChange(filter, search));
  }

  resetSearch() {
    if (this.searchGrid.dataGrid.searchText) {
      this.alert.add({
        text: gettext('Search reset. Full text search does not support filtering.'),
        type: Status.WARNING,
        timeout: this.WARNING_TIMEOUT_TIME
      } as Alert);
      this.searchText = '';
      this.searchGrid.dataGrid.searchText = '';
    }
  }

  resetFilter() {
    this.filter = '';
    if (this.searchGrid.dataGrid.filteringApplied) {
      this.alert.add({
        text: gettext('Filter reset. Full text search does not support filtering.'),
        type: Status.WARNING,
        timeout: this.WARNING_TIMEOUT_TIME
      } as Alert);
      this.searchGrid.dataGrid.clearFilters(false);
    }
  }

  ngOnDestroy() {
    this.unsubscribe$.next();
    this.unsubscribe$.complete();
  }

  private onQueryParamsChange(filter: string, searchTerm: string) {
    if (!this.shouldFilter(filter) && searchTerm) {
      this.searchText = searchTerm || '';
    }
  }

  private shouldFilter(filter) {
    if (!filter) {
      return false;
    }
    this.resetSearch();
    this.filter = filter || '';
    this.searchGrid.updateFiltering(['name'], {
      type: FilteringActionType.ApplyFilter,
      payload: {
        filteringModifier: {
          externalFilterQuery: {
            names: [this.filter]
          }
        }
      }
    });
    return true;
  }
}
