import { Component, EventEmitter, Inject, Optional } from '@angular/core';
import { IManagedObject, IResultList } from '@c8y/client';
import { NavigationExtras, Router } from '@angular/router';
import { RouterService as C8yRouter } from '@c8y/ngx-components';
import { Observable, UnaryFunction } from 'rxjs';
import { SearchConfig, SEARCH_CONFIG } from './search-config.model';

@Component({
  selector: 'c8y-search-action',
  templateUrl: './search-action.component.html'
})
export class SearchActionComponent {
  showAdvancedFilters: boolean;
  customPlaceholder: string;
  customQuery: UnaryFunction<string, Observable<IResultList<IManagedObject>>>;
  typeaheadReload = new EventEmitter();

  constructor(
    private c8yRouter: C8yRouter,
    private router: Router,
    @Optional() @Inject(SEARCH_CONFIG) public moduleConfig: SearchConfig
  ) {
    this.showAdvancedFilters = moduleConfig?.showAdvancedFilters ?? true;
    this.customPlaceholder = moduleConfig?.placeholder;
  }

  triggerDataLoad() {
    this.typeaheadReload.next(null);
  }

  onSearch(on: string) {
    this.navigate(['/assetsearch'], {
      queryParams: { search: on },
      replaceUrl: true
    });
  }

  onFilter(on: string) {
    this.navigate(['/assetsearch'], {
      queryParams: { filter: on },
      replaceUrl: true
    });
  }

  onClick(mo: IManagedObject) {
    this.router.navigateByUrl(this.c8yRouter.getHref(mo, '/'));
  }

  private navigate(commands: any[], extras?: NavigationExtras) {
    this.router
      .navigateByUrl('/', { skipLocationChange: true })
      .then(() => this.router.navigate(commands, extras));
  }
}
