import { Component, EventEmitter, Inject, Input, Optional, Output, ViewChild } from '@angular/core';
import { IManagedObject, SmartGroupsService } from '@c8y/client';
import {
  ActionControl,
  BuiltInActionType,
  BulkActionControl,
  Column,
  DataGridComponent,
  DataSourceModifier,
  DATA_GRID_CONFIGURATION_CONTEXT_PROVIDER,
  DATA_GRID_CONFIGURATION_STRATEGY,
  FilteringActionType,
  FilteringModifier,
  gettext,
  GridConfigContextProvider,
  Pagination,
  Row,
  SearchFilters,
  ServerSideDataResult,
  UserPreferencesConfigurationStrategy,
  UserPreferencesGridConfigContext
} from '@c8y/ngx-components';
import {
  AlarmsDeviceGridColumn,
  ImeiDeviceGridColumn,
  ModelDeviceGridColumn,
  NameDeviceGridColumn,
  RegistrationDateDeviceGridColumn,
  SerialNumberDeviceGridColumn,
  SystemIdDeviceGridColumn
} from '@c8y/ngx-components/device-grid';
import {
  DeleteAssetsModalComponent,
  DeleteModalCheckboxes,
  SubAssetsService
} from '@c8y/ngx-components/sub-assets';
import { BsModalService } from 'ngx-bootstrap/modal';
import { AssetTypeSearchGridColumn } from './columns/asset-type-search-grid-column';
import { SearchConfig, SEARCH_CONFIG } from './search-config.model';
import { AssetSearchService } from './search.service';

@Component({
  selector: 'c8y-search-grid',
  templateUrl: './search-grid.component.html',
  providers: [
    {
      provide: DATA_GRID_CONFIGURATION_STRATEGY,
      useClass: UserPreferencesConfigurationStrategy
    },
    {
      provide: DATA_GRID_CONFIGURATION_CONTEXT_PROVIDER,
      useExisting: SearchGridComponent
    }
  ]
})
export class SearchGridComponent implements GridConfigContextProvider {
  @Input('parent-group') parentGroup: IManagedObject;
  @Input() title = '';
  @Input() loadingItemsLabel: string = gettext('Loading resultsâ€¦');
  @Input('columns') set _columns(value: Column[]) {
    if (value) {
      this.columns = value;
    } else {
      this.columns = this.assetSearchService.getDefaultColumns();
    }
  }
  @Input('pagination') set _pagination(value: Pagination) {
    if (value) {
      this.pagination = value;
    }
  }
  @Input('actionControls') set _actionControls(value: ActionControl[]) {
    if (value) {
      this.actionControls = value;
    } else {
      this.actionControls = this.assetSearchService.getDefaultActionControls();
    }
  }
  @Input() selectable = false;
  @Input('bulkActionControls') set _bulkActionControls(value: BulkActionControl[]) {
    if (value) {
      this.bulkActionControls = value;
    } else {
      this.bulkActionControls = this.assetSearchService.getDefaultBulkActionControls();
    }
  }
  @Output() onColumnsChange: EventEmitter<Column[]> = new EventEmitter<Column[]>();

  @Input()
  searchText = '';

  @Input()
  filteringName: string;

  /** The name of the key where columns configuration will be stored. */
  @Input() columnsConfigKey: string;

  columns: Column[];
  pagination: Pagination = this.assetSearchService.getDefaultPagination();
  actionControls: ActionControl[];
  bulkActionControls: BulkActionControl[] = this.assetSearchService.getDefaultBulkActionControls();
  showAdvancedFilters: boolean;
  customPlaceholder: string;
  serverSideDataCallback: any;
  refresh: EventEmitter<any> = new EventEmitter();

  @ViewChild(DataGridComponent, { static: true })
  dataGrid: DataGridComponent;

  private sizeCount = 0;

  constructor(
    public assetSearchService: AssetSearchService,
    private bsModalService: BsModalService,
    private smartGroupsService: SmartGroupsService,
    private subAssetsGridService: SubAssetsService,
    @Optional() @Inject(SEARCH_CONFIG) public moduleConfig: SearchConfig
  ) {
    this.showAdvancedFilters = moduleConfig?.showAdvancedFilters ?? false;
    this.customPlaceholder = moduleConfig?.placeholder ?? undefined;
  }

  getGridConfigContext(): UserPreferencesGridConfigContext {
    return { key: this.columnsConfigKey || this.assetSearchService.GRID_CONFIG_STORAGE_KEY };
  }

  ngOnInit(): void {
    if (!this.filteringName) {
      this.columns = [
        new AssetTypeSearchGridColumn(
          this.showAdvancedFilters,
          { sortOrder: 'desc' },
          this.assetSearchService,
          this.customPlaceholder
        ),
        ...this.assetSearchService.getDefaultColumns()
      ];
    } else {
      this.columns = [
        new AssetTypeSearchGridColumn(
          this.showAdvancedFilters,
          { sortOrder: 'desc' },
          this.assetSearchService
        ),
        new NameDeviceGridColumn({
          sortOrder: 'asc',
          filter: { externalFilterQuery: { names: [this.filteringName] } }
        }),
        new ModelDeviceGridColumn(),
        new SerialNumberDeviceGridColumn({ visible: false }),
        new RegistrationDateDeviceGridColumn({ visible: false }),
        new SystemIdDeviceGridColumn({ visible: false }),
        new ImeiDeviceGridColumn({ visible: false }),
        new AlarmsDeviceGridColumn()
      ];
    }
    this.serverSideDataCallback = this.onDataSourceModifier.bind(this);
    this.setActionControls();
  }

  ngAfterViewInit() {
    this.setInitialFilterForTypeColumn();
  }

  trackByName(_index, column: Column): string {
    return column.name;
  }

  async onDataSourceModifier(
    dataSourceModifier: DataSourceModifier
  ): Promise<ServerSideDataResult> {
    const response = await this.assetSearchService.getData(
      dataSourceModifier.columns,
      dataSourceModifier.pagination,
      dataSourceModifier.searchText
    );
    const { res, data, paging } = response;

    if (paging.currentPage === 1) {
      this.sizeCount = 0;
    }
    this.sizeCount += data.length;
    this.onColumnsChange.emit(dataSourceModifier.columns);

    return {
      res,
      data,
      paging,
      filteredSize: this.sizeCount,
      size: undefined
    };
  }

  setActionControls() {
    const actionControls: ActionControl[] = [];
    const deleteAction: ActionControl = {
      type: BuiltInActionType.Delete,
      callback: (asset: Row) => this.onDeleteAsset(asset as IManagedObject, this.parentGroup)
    };
    actionControls.push(deleteAction);
    if (!this.actionControls) {
      this.actionControls = actionControls;
    }
  }

  updateFiltering(
    columnNames: string[],
    action: {
      type: FilteringActionType;
      payload?: { filteringModifier: FilteringModifier };
    }
  ) {
    const { type } = action;
    if (type === FilteringActionType.ResetFilter) {
      this.dataGrid.clearFilters();
    } else {
      /**
       * TODO: find better solution. After new changes from DM team, we're running into race condition where
       * this.dataGrid.updateFiltering is executed before this.configurationStrategy.getConfig$() value is emitted.
       * Columns setter sets columns after this.dataGrid.updateFiltering executes its logic. Value of this.columns in
       * dataGrid.updateFiltering is just not yet set.
       */
      setTimeout(() => {
        this.dataGrid.updateFiltering(columnNames, action, false);
      }, 500);
    }
  }

  onColumnFilterReset(column: Column) {
    if (column.name === 'type') {
      this.assetSearchService.resetAppliedFilters();
    }
  }

  private onDeleteAsset(asset, parentRef) {
    const initialState = {
      showWithDeviceUserCheckbox: this.subAssetsGridService.shouldShowWithDeviceUserCheckbox(asset),
      asset,
      showWithCascadeCheckbox:
        !this.smartGroupsService.isSmartGroup(asset) &&
        !this.smartGroupsService.isSmartGroupV2(asset)
    };

    const modalRef = this.bsModalService.show(DeleteAssetsModalComponent, { initialState });

    modalRef.content.closeSubject.subscribe(async (result: DeleteModalCheckboxes) => {
      if (result) {
        await this.subAssetsGridService.deleteAsset(asset, parentRef, result);
        this.refresh.emit();
      }
    });
  }

  private setInitialFilterForTypeColumn() {
    const checkboxes = this.assetSearchService.appliedFilters$.value;
    // Set filter only when all checkboxes are not selected
    if (
      checkboxes[SearchFilters.ONLY_DEVICES] !== checkboxes[SearchFilters.ONLY_GROUPS_AND_ASSETS]
    ) {
      const externalFilterQuery = {
        [SearchFilters.ONLY_DEVICES]: checkboxes[SearchFilters.ONLY_DEVICES],
        [SearchFilters.ONLY_GROUPS_AND_ASSETS]: checkboxes[SearchFilters.ONLY_GROUPS_AND_ASSETS]
      };
      this.updateFiltering(['type'], {
        type: FilteringActionType.ApplyFilter,
        payload: {
          filteringModifier: {
            externalFilterQuery
          }
        }
      });
    }
  }
}
